import { request } from 'utils';

const prefix = '/api/categories';

export const getWards = ({
  districtId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/Ward',
    method: 'GET',
    params: { districtId, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const getWardById = id =>
  request({
    prefix,
    url: `/Ward/${id}`,
    method: 'GET',
  });

export const createWard = data =>
  request({
    prefix,
    url: '/Ward',
    method: 'POST',
    data,
  });

export const updateWard = (id, data) =>
  request({
    prefix,
    url: `/Ward/${id}`,
    method: 'PUT',
    data: { ...data, id },
  });

export const deleteWard = id =>
  request({
    prefix,
    url: `/Ward/${id}`,
    method: 'DELETE',
  });



