import React, { Fragment, useState } from 'react';
import { Link } from 'react-router-dom';
import routeConstant from 'routes/route/routeConstant';
import { TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, message } from 'antd';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { getWards, deleteWard } from 'services/ward';
import { getQueryParams } from 'utils/urlUtil';
import SearchBar from './SearchBar';
import './style.scss';

const Ward = () => {
  const [reload, setReload] = useState(0);
  const [loading, setLoading] = useState(false);
  const paramSearch = getQueryParams();

  const handleDelete = async id => {
    try {
      setLoading(true);
      await deleteWard(id);
      setLoading(false);
      setReload(reload + 1);
      message.success('Xóa thành công');
    } catch (err) {
      message.error(err.message.reason);
    }
  };

  const columns = [
    {
      title: 'Tên phường/xã',
      dataIndex: 'label',
      key: 'label',
    },
    {
      title: 'Thao tác',
      dataIndex: 'operation',
      render: (text, record) => (
        <Fragment>
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: routeConstant.DANH_MUC__TUYEN_SINH__PHUONG_XA__NHAP_DU_LIEU,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => handleDelete(record.id)}
          />
        </Fragment>
      ),
    },
  ];

  return (
    <Card title="Danh mục phường xã">
      <Row>
        <Col span={24}>
          <Link to={routeConstant.DANH_MUC__TUYEN_SINH__PHUONG_XA__NHAP_DU_LIEU}>
            <BtnAddNew />
          </Link>
        </Col>
      </Row>
      <hr />
      <Row>
        <SearchBar />
      </Row>
      <Row>
        <Col span={24}>
          <TableData
            loading={loading}
            reload={reload}
            service={getWards}
            columns={columns}
            paramSearch={paramSearch}
          />
        </Col>
      </Row>
    </Card>
  );
};

export default props => (
  <Page {...props}>
    <Ward {...props} />
  </Page>
);



