import React, { Fragment, useEffect } from 'react';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import { Form, Row, Col } from 'antd';
import { BtnSearch } from 'components/common/button';
import { SelectAjax } from 'components/common';
import { getDistricts } from 'services/district';
import { getListTown } from 'services/town';

const formItemLayout = {
  labelCol: {
    xs: { span: 24 },
    sm: { span: 7 },
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 17 },
  },
};

const SearchBar = props => {
  const { form } = props;
  const { getFieldDecorator, getFieldValue, setFieldsValue } = form;
  const townId = getFieldValue('townId');

  useEffect(() => {
    const queryParams = getQueryParams();
    delete queryParams._time;
    form.setFieldsValue(queryParams);
  }, []);

  const handleSearch = e => {
    e.preventDefault();
    form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };

  const getTowns = () => {
    return getListTown({ isPagingEnabled: false });
  };

  return (
    <Fragment>
      <Form className="form-inline-label" layout="vertical" onSubmit={handleSearch}>
        <Row gutter={24}>
          <Col span={8}>
            <Form.Item {...formItemLayout} label="Tỉnh thành">
              {getFieldDecorator('townId', {})(<SelectAjax service={getTowns} />)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item {...formItemLayout} label="Quận/Huyện">
              {getFieldDecorator('districtId', {})(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getDistricts}
                  params={{ townId }}
                  disabled={!townId}
                  allowGetData={!!townId}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
            </div>
          </Col>
        </Row>
      </Form>
    </Fragment>
  );
};

export default Form.create()(SearchBar);



